<template>
  <div class="ratingselect">
    <div class="rating-type">
      <span @click="select(2,$event)" class="block positive" :class="{'active':selectType===2}">
        {{desc.all}}
        <span class="count">{{ratings.length}}</span>
      </span>
      <span @click="select(0,$event)" class="block positive" :class="{'active':selectType===0}">
        {{desc.positive}}
        <span class="count">{{positives.length}}</span>
      </span>
      <span @click="select(1,$event)"class="block negative" :class="{'active':selectType===1}">
        {{desc.negative}}
        <span class="count">{{negatives.length}}</span>
      </span>
    </div>
    <div class="switch" :class="{'on':onlyContent}">
      <span class="icon-check_circle"></span>
      <span @click="toggleContent($event)" class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script type='text/ecmascript-6'>
  //定义三个类型的常量
  const POSITIVE = 0;
  const NEGATIVE = 1;
  const ALL = 2;
  
  export default {
    props: {
      ratings: {
        type: Array,
        //对象则需函数返回默认值
        default () {
          return [];
        }
      },
      selectType: {
        type: Number,
        default: ALL
      },
      onlyContent: {
        type:Boolean,
        default: false
      },
      desc: {
        type: Object,
        default() {
          return {
            all: '全部',
            positive: '满意',
            negative: '不满意'

          };
        }
      }
    },
    methods: {
      select(type,event) {
        this.$emit('select',type);
      },
      toggleContent() {
        this.$emit('toggle');
      }
    },
    computed: {
      positives() {
         return this.ratings.filter( (rating) => {
          return rating.rateType === POSITIVE;
        });
      },
      negatives() {
        return this.ratings.filter( (rating) => {
          return rating.rateType === NEGATIVE;
        });
      }
    }
  };
</script>

<style lang='stylus' rel='stylesheet/stylus'>
  @import  "../../common/stylus/mixin.styl"
  .ratingselect
    .rating-type
      padding: 18px 0
      margin: 0 18px
      border-1px(rgba(7,17,27,0.1))
      font-size: 0
      .block
        display: inline-block
        padding: 8px 12px
        margin-right: 8px
        line-height: 16px
        font-size: 12px
        border-radius: 1px
        color:rgb(77,85,93)
        &.active
          color: #fff
        &.positive
          background:rgba(0,160,220,0.2)
          &.active
            background: rgb(1,160,220)
        &.negative
          background: rgba(77,85,93,0.2)
          &.active
            background:rgb(77,85,93)
        .count
          margin-left: 2px
          font-size: 8px
    .switch
      padding: 12px 18px
      line-height: 24px
      font-size: 0px
      border-bottom: 1px solid rgba(7,17,27,0.1)
      &.on
        .icon-check_circle 
          color: #00c850
     .icon-check_circle
        display: inline-block
        margin-right: 4px
        vertical-align: top
        font-size: 24px
        color: rgb(147,153,159)
      .text
        display: inline-block
        vertical-align: top
        font-size: 12px
        
       
</style>



