<template>
  <div class="header">
    <div class="content-wrapper">
      <div class="avatar">
        <img width="64" height="64" :src="seller.avatar" alt="">
      </div>
      <div class="content">
        <div class="title">
          <span class="brand"></span>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">
          {{seller.description}}/{{seller.deliveryTime}}分钟送达
        </div>
        <div v-if="seller.supports" class="support">
          <span class="icon" :class="classMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>
      <div v-if="seller.supports" class="support-count" @click="showDetail">
        <span class="count">{{seller.supports.length}}个</span>
        <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <div class="bulletin-wrapper"  @click="showDetail">
      <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
    <div class="background">
      <img :src="seller.avatar" alt="" width="100%" height="100%">
    </div>
    <transition name="fade">
      <div v-show="detailShow" class="detail">
        <!--注意要给detail-wrapper添加clearfix类而不是给detail添加clearfix类 -->
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="name">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
            </div>
            <div class="title">
              <div class="line"></div>
              <div class="text">优惠信息</div>
              <div class="line"></div>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li class="support-item" v-for="(item,index) in seller.supports" v-bind:key="index">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <div class="title">
              <div class="line"></div>
              <div class="text">商家公告</div>
              <div class="line"></div>
            </div>
            <div class="bulletin">
              <p class="content">{{seller.bulletin}}</p>
            </div>
            
          </div>
        </div>
        <div class="detail-close" @click="hideDetail">
          <i class="icon-close"></i>
        </div>
      </div>
    </transition>
  </div>
</template>

<script type='text/ecmascript-6'>
import star from 'components/star/star.vue'
export default {
  props: {
    seller: {
      type: Object  
    }
  },
  data () {
    return {
      detailShow: false
    }
  },
  methods: {
    showDetail () {
      this.detailShow = true;
    },
    hideDetail () {
      this.detailShow = false;
    }
  },
  created () {
      this.classMap = ['decrease','discount','guarantee','invoice','special'];
  },
  components: {
    star
  }
};

</script >
  
<style lang='stylus' rel='stylesheet/stylus'>
@import "../../common/stylus/mixin.styl"
.header
  position: relative
  color:#fff
  background: rgba(7,17,27,0.5)
  overflow: hidden
  .content-wrapper
    position: relative
    padding: 24px 12px 18px 24px
    font-size : 0
    .avatar
      display: inline-block
      img 
       border-radius: 2px
    .content
      display: inline-block
      margin-left :16px
      font-size :14px
      .title
        margin-top:2px
        margin-bottom : 8px
        .brand
          display: inline-block
          width: 30px
          height: 18px
          bg-image('brand')
          background-size: 30px 18px
          background-repeat: no-repeat
        .name
          display: inline-block
          vertical-align: top
          margin-left: 6px
          font-size:16px
          color: rgb(255,255,255)
          font-weight: bold
          line-height: 18px
      .description
        margin-bottom: 10px
        font-size: 12px
        line-height: 12px
      .support
        .icon
          display: inline-block
          margin-right: 4px
          height: 12px
          width: 12px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decrease
            bg-image('decrease_1')
          &.discount
            bg-image('discount_1')
          &.guarantee
            bg-image('guarantee_1')
          &.invoice
            bg-image('invoice_1')
          &.special
            bg-image('special_1')
        .text 
          line-height: 12px
          vertical-align: top
          font-size: 10px
          color: rgb(255,255,255)
    .support-count
      position: absolute 
      right: 12px
      bottom: 18px
      padding: 0 8px
      height: 24px
      line-height: 24px
      border-radius: 14px
      background: rgba(0,0,0,0.2)
      text-align: center
      .count
        font-size: 10px
      .icon-keyboard_arrow_right
        font-size: 10px
        margin-left:2px
        line-height: 24px
  .bulletin-wrapper
    position: relative
    background: rgba(7,17,27,0.2)
    height: 28px
    line-height: 28px
    padding: 0 12px 
    white-space: nowrap 
    overflow: hidden  
    text-overflow: ellipsis
    .bulletin-title
      display: inline-block
      vertical-align: top
      margin-top: 8px
      height: 12px
      width: 22px
      bg-image('bulletin') 
      background-size:22px 12px
      background-repeat: no-repeat
    .bulletin-text
      vertical-align: top
      margin:0 4px
      font-size: 10px
    .icon-keyboard_arrow_right
      position: absolute
      display: inline-block
      right: 6px
      font-size: 10px
      top: 8px
  .background
    position: absolute
    top: 0
    left: 0 
    width: 100%
    height: 100%   
    z-index: -1
    filter: blur(10px)
  .detail
    position: fixed
    top: 0
    left: 0
    z-index: 100
    height: 100%
    width: 100%
    overflow: auto
    backdrop-filter: blur(10px);
    background:rgba(7,17,27,0.8)
    &.fade-enter-active, &.fade-leave-active 
      transition: all .5s
    &.fade-enter, &.fade-leave-to
      opacity: 0
      background:rgba(7,17,27,0)
    .detail-wrapper
      min-height: 100%
      width: 100%
      .detail-main
        margin-top: 64px
        padding-bottom: 64px
        .name
          font-size: 16px
          font-weight: 700
          color: rgb(255,255,255)
          line-height: 16px
          text-align: center      //让文字水平居中
        .star-wrapper
          margin-top: 18px
          padding: 2px 0
          text-align: center
        .title
          display: flex
          width: 80%
          margin: 28px auto 24px auto
          .line
            flex: 1
            position: relative
            top: -6px
            border-bottom: 1px solid rgba(255,255,255,0.2)
          .text 
            padding: 0 12px
            font-size: 14px
            font-weight: 700
        .supports
          width: 80%
          margin: 0 auto
          .support-item
            padding: 0 12px
            margin-bottom: 12px
            font-size: 0
            &:last-child
              margin-bottom: 0
            .icon
              display: inline-block
              width: 16px
              height: 16px
              vertical-align: top
              margin-right: 6px
              background-size: 16px 16px
              background-repeat: no-repeat
              &.decrease
                bg-image('decrease_2')
              &.discount
                bg-image('discount_2')
              &.guarantee
                bg-image('guarantee_2')
              &.invoice
                bg-image('invoice_2')
              &.special
                bg-image('special_2')
            .text
              font-size: 12px 
              color: rgb(255,255,255)
              line-height: 16px
        .bulletin
          width: 80%
          margin: 0 auto
          .content
            padding: 0 12px
            font-size: 12px
            line-height: 24px
              
                


    .detail-close
      position: relative
      width: 32px
      height: 32px
      margin:-64px auto 0 auto
      clear: both
      font-size: 32px
      
      
      
          
     
          
        


 


</style>



