<template>
<div class="seller" ref="seller">
    <div class="seller-content">
        <div class="overview">
            <h1 class="title">{{seller.name}}</h1>
            <div class="desc border-1px">
                <star :size="36" :score="seller.score"></star>
                <span class="text">({{seller.ratingCount}})</span>
                <span class="text">月售{{seller.sellCount}}单</span>
            </div>
            <ul class="remark">
                <li class="block">
                    <h2>起送价</h2>
                    <div class="content">
                        <span class="stress">{{seller.minPrice}}</span>元
                    </div>
                </li>
                <li class="block">
                    <h2>商家配送</h2>
                    <div class="content">
                        <span class="stress">{{seller.deliveryPrice}}</span>元
                    </div>
                </li>
                <li class="block">
                    <h2>平均配送时间</h2>
                    <div class="content">
                        <span class="stress">{{seller.deliveryTime}}</span>分钟
                    </div>
                </li>
            </ul>
            <div class="favorite"  @click="toggleFavorite">
                <span class="icon-favorite" :class="{'active':favorite}"></span>
                <span class="text">{{favoriteText}}</span>
            </div>
        </div>
        <split></split>
        <div class="bulletin">
            <h1 class="title">公告与活动</h1>
            <div class="content-wrapper border-1px">
                <p class="content">{{seller.bulletin}}</p>
            </div>
            <ul v-if="seller.supports" class="supports">
              <li class="support-item border-1px" v-for="(item,index) in seller.supports" v-bind:key="index">
                <span class="icon" :class="classMap[seller.supports[index].type]"></span>
                <span class="text">{{seller.supports[index].description}}</span>
              </li>
            </ul>
        </div>
        <split></split>
        <div class="pics">
            <h1 class="title">商家实景</h1>
            <div class="pic-wrapper" ref="picWrapper">
                <ul class="pic-list" ref="picList">
                    <li class="pic-item" v-for="pic in seller.pics">
                        <img :src="pic" width="120px" height="90px">
                    </li>
                </ul>
            </div>
        </div>
        <split></split>
        <div class="info">
            <h1 class="title border-1px">商家信息</h1>
            <ul>
                <li class="info-item" v-for="info in seller.infos">{{info}}</li>
            </ul>
        </div>
    </div>
</div>
</template>

<script type='text/ecmascript-6'>
import BScroll from "better-scroll"
import star from "components/star/star.vue"
import split from "components/split/split.vue"
import {saveToLocal, loadFromLocal} from 'common/js/store.js';
export default {
    props: {
        seller: {
            type: Object
        }
    },
    data() {
        return {
            favorite: (() => {
                return loadFromLocal(this.seller.id, 'favorite', false);
            })()
        };
    },
    methods: {
        toggleFavorite(event) {
            this.favorite = !this.favorite;
            saveToLocal(this.seller.id, 'favorite', this.favorite);
        },
        _initScroll() {
            if(!this.scroll) {
                this.scroll = new BScroll(this.$refs.seller,{
                    click: true
                });
            }else {
                this.scroll.refresh();
            }
        },
        _initPics() {
        if (this.seller.pics) {
            let picWidth = 120;
            let margin = 6;
            let width = (picWidth + margin) * this.seller.pics.length - margin;
            this.$refs.picList.style.width = width + 'px';
            this.$nextTick(() => {
                if (!this.picScroll) {
                    this.picScroll = new BScroll(this.$refs.picWrapper, {
                        scrollX: true,
                        eventPassthrough: 'vertical'
                    });
                } else {
                    this.picScroll.refresh();
                }
            });
        }
      }
    },
    computed: {
        favoriteText() {
            return this.favorite ? "已收藏" : "未收藏";
        }
    },
    components: {
        star,
        split

    },
    created() {
        this.classMap = ['decrease','discount','guarantee','invoice','special'];
    },
    watch: {
        'seller'() {
            this.$nextTick(() => {
                this._initScroll();
                this._initPics();
            
            });
        }
    },
    mounted() {
        this.$nextTick( () => {
            this._initScroll();
            this._initPics();
        })
    }
};
</script>

<style lang='stylus' rel='stylesheet/stylus'>
     @import "../../common/stylus/mixin.styl"
     
    .seller
        width: 100%
        position: absolute
        top: 174px
        bottom: 0px
        left: 0
        overflow: hidden
        .overview
            padding: 18px
            position: relative
            .title
                margin-bottom: 8px
                font-size: 14px
                color:rgb(7,17,27)
                line-height: 14px
            .desc
                padding-bottom: 18px
                border-1px(rgba(7,17,27,0.1))
                font-size: 0
                .star,.text
                    display: inline-block
                .star
                    margin-right: 8px
                    vertical-align: top
                .text
                    margin-right: 12px
                    font-size: 10px
                    line-height: 18px
                    color: rgb(77,85,93)
                    vertical-align: top
            .remark
                display: flex
                padding-top: 18px 
                .block
                    display: inline-block
                    flex: 1                                               
                    text-align: center
                    border-right: 1px solid rgba(7,17,27,0.1)
                    &:last-child
                        border: none
                    h2      
                        margin-bottom: 4px
                        font-size: 10px
                        line-height: 10px
                        color: rgb(147,153,159)
                    .content
                        line-height: 24px
                        font-size: 10px
                        color: rgb(7, 17, 27)
                        .stress
                            font-size: 24px
                            line-height: 24px
                            color: rgb(7,17,27)
                            font-weight: 200
            .favorite
                position: absolute
                width: 50px
                right: 11px
                top: 18px
                text-align: center
                .icon-favorite
                    display: block
                    margin-bottom: 4px
                    line-height: 24px
                    font-size: 24px
                    color: #d4d6d9
                    &.active
                        color: rgb(240, 20, 20)
                .text
                    line-height: 10px
                    font-size: 10px
                    color: rgb(77, 85, 93)
        .bulletin
            padding: 18px 18px 0 18px
            .title
                margin-bottom: 8px
                font-size: 14px
                color:rgb(7,17,27)
                line-height: 14px 
            .content-wrapper 
                padding: 0 12px 16px 12px
                border-1px(rgba(7,17,27,0.1))
                .content
                    font-size: 12px
                font-weght: 200
                color:rgb(240,20,20)
                line-height: 24px
            .supports
                .support-item
                    padding: 16px 12px
                    border-1px(rgba(7,17,27,0.1))
                    &:last-child
                        border-none()
                    .icon
                        display: inline-block
                        width: 16px
                        height: 16px
                        vertical-align: top
                        margin-right: 6px
                        background-size: 16px 16px
                        background-repeat: no-repeat
                        &.decrease
                            bg-image('decrease_4')
                        &.discount
                            bg-image('discount_4')
                        &.guarantee
                            bg-image('guarantee_4')
                        &.invoice
                            bg-image('invoice_4')
                        &.special
                            bg-image('special_4')
                    .text
                        vertical-align: top
                        font-size: 12px
                        font-weight: 200
                        color: rgb(7,17,27)
                        line-height: 16px
        .pics
            padding: 18px
            .title
                line-height: 14px
                font-size: 14px
                color: rgb(7,17,27)
                margin-bottom: 12px
            .pic-wrapper
                width: 100%
                over-flow: hidden
                //保证图片在一行
                white-space: nowrap
                .pic-list
                    font-size: 0
                    .pic-item
                        display: inline-block
                        margin-right: 6px
                        width: 120px
                        height: 90px
                        &:last-child
                            margin: 0
        .info
            padding: 18px
            .title
                line-height: 14px
                font-size: 14px
                color: rgb(7,17,27)
                padding-bottom: 12px
                border-1px(rgba(7,17,27,0.1))
            ul
                .info-item
                    padding: 16px 12px
                    border-1px(rgba(7,17,27,0.1))
                    font-size: 12px
                    line-height: 16px
                    font-weight: 200
                    color: rgb(7,17,27)
                    &:last-child
                        padding-bottom: 0
                        border-none()


                                                              
          

</style>



